.app_container{
    margin: 10vh auto;
    width: 100%;
    display: flex;
    justify-content: center;
}

.app{
    width: 15%;
}

.buttons button{
    padding: 4% 3%;
    background-color: lightblue;
    cursor:url(http://www.rw-designer.com/cursor-view/82421.png), auto;
    transition: 0.3s;
}

.buttons{
    width: 100%;
    display: flex;
    justify-content: space-between;
}

.buttons button:hover{
    background-color: #52c5ea;
    transition: 0.3s;
}